import styled from 'styled-components';
import WithDirection from '../../../settings/withDirection';

const TableStyleWrapper = styled.div`
  tr.ant-table-row-level-0 {
    background: #f17d27;
  }
  .ant-table-tbody {
    font-size: 12px;
  }
  .ant-table-thead {
    font-size: 12px;
  }

  .ant-table-row-level-1 > .highLight {
    background: #0b2345;
  }

  .ant-table-thead > tr > th.highLight {
    background: #0b2345 !important;
  }
`;

export default WithDirection(TableStyleWrapper);
